* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Roboto', serif;  
  font-weight: 300;
}
h1 {
  height: 100%;
  margin: 0;
  text-align: center; 
  font-size: 4.5em;
  font-weight: 100;
}
b {
  font-weight: 400;
}
h2 {
  height: 100%;
  margin: 0;
  font-size: 3.5em;
  font-weight: 300;
}
h3 {
  height: 100%;
  margin: 0;
  font-size: 1.5em;
  font-weight: 300;
}
h4 {
  height: 100%;
  margin: 0;
  font-size: 2em;
  font-weight: 100;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  text-align: center;
  padding: 0;
}
ul.header li a {
  color: #000;
  font-size: 1.5em;
  font-weight: 300;
  text-decoration: none;
  padding: 10px;
  display: inline-block;
  outline: none;
}
ul.header li a:hover {
  font-style: italic;
}
ul.header li a.active {
  font-weight: 500;
}
.content {
  display: block;
  max-width: 1000px;
  padding-left: auto;
  padding-right: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 1em;
  width: 100%;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}
.social {
  text-align: center;
  margin-bottom: 1em;
}
.social a {
  margin-right: 1em;
}
.social a img {
  width: 25px;
  margin-bottom: 1em;
}
.movies,.shows,.games,.books,.boardgames,.year,.term {
  float: left;
}
.term {
  margin-left: 1em;
}
.title {
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
}
.finalYear {
  float: inline-start;
}
canvas {
  padding: 0;
  margin: auto;
  display: block;
}



